@mixin textStyle {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;


}

.movie-info-container.load {
    width: 100%;
    align-items: center;
    justify-content: center;

}

.movie-info-container {
    width: 100%;
    color: white;
    display: flex;
    padding: 20px 60px;
    justify-content: left;

    .adaptive-img {
        display: none;
    }


    .porster-img {
        width: 300px;
        height: 450px;
    }

    .info-movie {
        margin-left: 45px;



        .title-movie {
            font-family: 'Roboto Condensed', sans-serif;
            font-size: 40px;
            font-weight: 700;
            line-height: 44px;
            letter-spacing: 0em;
            text-align: center;
        }

        .genres-time {
            @include textStyle;
            margin-top: 10px;

            .age-ocntainer {
                margin-top: 20px;

                .ageSpan {

                    font-family: 'Roboto Condensed', sans-serif;
                    font-size: 30px;
                    font-weight: 700;
                    line-height: 44px;
                    letter-spacing: 0em;
                    border: 1px solid white;
                    padding: 5px;
                    border-radius: 30%;
                }
            }
        }

        .slogan-block {
            margin-top: 20px;

            .slogan-title {
                font-family: 'Roboto Condensed';
                font-style: normal;
                font-weight: 700;
                font-size: 20px;
                line-height: 24px;
            }

            .slogan {
                @include textStyle;
                margin-top: 10px;
            }
        }

        .container-score {
            display: flex;
            align-items: center;
            margin-top: 55px;
            justify-content: space-between;

            .rating-kinopoisk {
                width: 80px;
                height: 95px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                background: rgba(217, 217, 217, 0.1);
                border: 1px solid white;
                border-radius: 8px;

                padding: 15px;

                .num-rating {
                    margin-top: 10px;
                }
            }

            .rating-kinopoisk.red {
                border: 1px solid red;
            }

            .rating-kinopoisk.green {
                border: 1px solid green;
            }

            .rating-kinopoisk.yellow {
                border: 1px solid yellow;
            }

            .add-to-wachlist {
                width: 200px;
                height: 50px;
                font-family: 'Roboto Condensed', sans-serif;
                font-style: normal;
                font-weight: 700;
                font-size: 16px;
                line-height: 19px;
                display: flex;
                align-items: center;
                text-align: center;
                color: #141414;
                background-color: #F33F3F;

                border-radius: 5px;

                justify-content: center;


            }
        }
    }
}


//media
@media screen and (max-width: 1000px)   {
    .movie-info-container {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 80%;
        padding: 0;
        margin: 10px;


        .info-movie {
            width: 100%;
            margin-left: 0;

            .title-movie {
                font-size: 30px;
                line-height: 30px;

            }
        }

        .porster-img {

            display: none;
        }

        .adaptive-img {
            display: block;
            margin-top: 20px;
            width: 300px;
            height: 450px;
           
        }

        .container-score {
            flex-direction: column;

            .add-to-wachlist {
                margin-top: 20px;
            }
        }
    }
}

@media screen and (min-width: 675px)  {
    .movie-info-container {

        .container-score {
            flex-direction: row;

            .adaptive-img {
                
                margin-top: 20px;
                width: 150px;
            height: 225px;

            }

            .add-to-wachlist {
                margin-top: 20px;
            }
        }
    }
}