@mixin textStyle {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;

    color: #E1E1E1;
}

.wel-container {
    margin-top: 35px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;


    .info {
        width: 85%;
        background: rgba(217, 217, 217, 0.1);
        padding: 20px;
        border: 1px solid #A41B1B;
        border-radius: 8px;
        font-family: 'Roboto Condensed', sans-serif;
        font-size: 40px;
        font-weight: 800;
        line-height: 44px;
        letter-spacing: 0em;
        text-align: center;

        .info-use {
            margin-top: 35px;
            @include textStyle;
        }
    }

    .input-movies {
        width: 85%;
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border: 1px solid rgba(217, 217, 217, 0.3);
        border-radius: 6px;
        color: #E1E1E1;




        div {
            display: flex;
            align-items: center;
            width: 100%;
            height: 100%;

            img {
                padding: 10px;
            }

            input {
                min-width: 95%;

            }
        }

        button {
            box-sizing: border-box;
            background: #F33F3F;
            color: #000000;
            border-radius: 6px;
            height: 41px;
            min-width: 7%;
        }
    }

    .popular-now {
        margin-top: 35px;
        @include textStyle;
        font-size: 35px;
        color: #E1E1E1;
        margin-bottom: 35px;
    }

    .movies-cards {
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
        width: 85%;
        justify-content: center;
    }
}

//media
@media screen and (max-width:1000px) {
    .wel-container {

        .info {
            font-size: 20px;
            line-height: 25px;

            .info-use {
                margin-top: 10px;
                font-size: 14px;
                line-height: 20px;
            }
        }

        .input-movies {
            button {
                width: 19%;
            }
        }

        .popular-now {
            font-size: 25px;
        }

        .movies-cards {
            width: 100%;
        }
    }

}